<template>
  <div class="viInputNumber">
    <InputNumber
      v-show="!isMobile"
      ref="input"
      v-model="inputValue"
      :placeholder="placeholder"
      :disabled="disabled"
      :min="min"
      :max="max"
      :step="step"
    ></InputNumber>
    <!-- min为空串清空默认值1 -->
    <van-stepper
      v-show="isMobile"
      v-model="inputValue"
      :placeholder="placeholder"
      :disabled="disabled"
      :min="min || ''"
      :max="max"
      :step="step"
      :integer="integer"
      button-size="32"
      allow-empty
      class="stepper"
    />
  </div>
</template>

<script>
import { InputNumber } from "view-ui-plus";
import { Stepper } from "vant";

export default {
  name: "viInputNumber",
  components: { InputNumber, [Stepper.name]: Stepper },
  emits: ["update:modelValue", "on-change"],
  props: {
    modelValue: {
      type: Number,
      default: null, // 清空默认值1
    },
    placeholder: {
      type: String,
      default: "请输入",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    integer: {
      type: Boolean,
      default: false,
    },
    min: Number,
    max: Number,
    step: {
      type: Number,
      default: 1,
    },
  },
  data() {
    return {
      isMobile: window.isMobile,
    };
  },
  computed: {
    inputValue: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit("update:modelValue", +val);
        this.$emit("on-change", +val);
      }
    },
  },
  methods: {
    focus() {
      this.$refs.input.$el.getElementsByTagName("INPUT")[0].focus();
    },
    keydown(e) {
      const value = +(this.inputValue || 0);
      if (e.key == "." && this.integer) {
        e.preventDefault();
      } else if (e.key == "Enter") {
        this.$emit("on-enter");
      } else if (
        (e.key == "+" || e.code == "NumpadAdd") &&
        (typeof this.max != "number" || value + this.step <= this.max)
      ) {
        this.inputValue = value + this.step;
        e.preventDefault();
      } else if (
        (e.key == "-" || e.code == "NumpadSubtract") &&
        (typeof this.min != "number" || value - this.step >= this.min)
      ) {
        this.inputValue = value - this.step;
        e.preventDefault();
      }
      this.$emit("on-keydown", e);
    },
  },
  mounted() {
    const element = this.$refs.input.$el.getElementsByTagName("INPUT")[0];
    element.addEventListener("keydown", this.keydown);
  },
  beforeUnmount() {
    const element = this.$refs.input.$el.getElementsByTagName("INPUT")[0];
    element.removeEventListener("keydown", this.keydown);
  },
};
</script>

<style lang="less" scoped>
.viInputNumber {
  display: inline-block;
  width: 100%;
  & > div {
    width: 100%;
  }
  .stepper {
    display: flex;
    :deep(input) {
      flex: 1;
    }
  }
}
</style>
